<!--  DOM  -->
<template>
    <div class="listCon">
        <!--  -->
        <template v-if="type=='fw'">
            <h5>{{obj.title}}</h5>
            <p><span>{{obj.court}}</span></p>
            <p><span>{{obj.caseNo}}</span></p>
            <p><span>{{obj.dateStr}}</span></p>
        </template>
        <!--  -->
        <template v-else-if="type=='zz'">
            <h5>{{obj.qualName}}</h5>
            <p><span>{{obj.certOrg}}</span></p>
            <p><span>{{obj.certNo}}</span></p>
            <p><span>{{obj.certDate}}</span></p>
        </template>
        <!--  -->
        <template v-else-if="type=='ry'">
            <h5>{{obj.name}}</h5>
            <p><span>{{obj.category}}</span></p>
            <p>  <span>{{obj.major}}</span></p>
            <p><span>{{obj.validDate}}</span></p>
        </template>
        <!--  -->
        <template v-else-if="type=='zb'">
            <h5>{{obj.title}}</h5>
            <p><span>{{obj.oneName}}</span></p>
            <p><span>{{obj.oneOffer}}</span></p>
            <p><span>{{obj.opendate}}</span></p>
        </template>
        <!--  -->
        <template v-else-if="type=='gb'">
            <h5>{{obj.mateName}}</h5>
            <p><span>{{obj.projName}}</span></p>
            <p><span>{{obj.years}}</span></p>
        </template>
        <!--  -->
          <template v-else-if="type=='bl'">
            <h5>{{obj.nature}}</h5>
            <p><span>{{obj.projectName}}</span></p>
            <p><span>{{obj.publishSite}}</span></p>
            <p><span>{{obj.publishDate}}</span></p>
            <p><span>{{obj.badBehaviorContent}}</span></p>
        </template>
        <!--  -->
        <template v-else>
            <h5>{{obj.proName}}</h5>
            <template v-if="type=='yj1'">
                <div><span class="zj label">{{obj.proType}}</span></div>
                <p><span>{{obj.proOrg}}</span></p>
                <p><span>{{obj.amount}}</span></p>
                <p><span>{{obj.buildEnd}}</span></p>
            </template>
            <template v-else-if="type=='yj2'">
                <div><span class="sl label">{{obj.proType}}</span></div>
                <p><span>{{obj.proOrg}}</span></p>
                <p><span>{{obj.amount}}</span></p>
                <p><span>{{obj.build}}</span></p>
            </template>
            <template v-else-if="type=='yj3'">
                <div><span class="jt label">{{obj.source}}</span></div>
                <p><span>{{obj.comName}}</span></p>
                <p><span>{{obj.section}}</span></p>
                <p><span>{{obj.amount}}</span></p>
                <p><span>{{obj.buildEnd}}</span></p>
            </template>
        </template>
    </div>
</template>
<script>
// let moment = require("moment");
export default {
    name: 'listCon', // 
    data() {
        return {
            // 
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        obj:{},
        type:{
            default:'fw'
        }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {

        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        // 
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.listCon{
    background: #fff;
    padding: 38px 20px;
    margin-bottom: 18px;
    border-radius: 8px;
    h5{
        font-size: 32px;
        // font-weight: normal;
        color: #000;
        margin-bottom: 26px;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space:nowrap
    }
    .label{
        padding: 11px 15px;
        font-size: 20px;
        margin-bottom: 24px;
        display: inline-block;
    }
    .zj{
        color: #EF873A;
        background: #FDEFE3;
    }
    .sl{
        background: #FFE5F0;
        color: #D65588;
    }
    .jt{
        background: #E3E6FD;
        color: #3A76F0;
    }
    p{
        font-size: 28px;
        color: #999;
        margin-bottom: 26px;
        overflow: hidden;
        span{
            color: #333;
        }
    }
    p:last-child{
        margin-bottom: 0
    }
}
</style>



