<!--  DOM  -->
<template>
    <div class="zhongbCon newNotice">
        <div @click="topush(obj)" >
            <div class="top">
                <p class="icon">
                    <img src="../assets/icon-yj.png"/>
                </p>
                <p class="tit">{{obj.proName}}</p>
            </div>
            <template v-if="type=='project'">
                <div><span class="zj label">{{obj.proType}}</span></div>
                <p><span>{{obj.proOrg}}</span></p>
                <p><span>{{obj.amount}}</span></p>
                <p><span>{{obj.buildEnd}}</span></p>
            </template>
            <template v-else-if="type=='shuili'">
                <div><span class="sl label">{{obj.proType}}</span></div>
                <p><span>{{obj.proOrg}}</span></p>
                <p><span>{{obj.amount}}</span></p>
                <p><span>{{obj.build}}</span></p>
            </template>
            <template v-else-if="type=='jiaotong'">
                <div><span class="jt label">{{obj.source}}</span></div>
                <p><span>{{obj.comName}}</span></p>
                <p><span>{{obj.section}}</span></p>
                <p><span>{{obj.amount}}</span></p>
                <p><span>{{obj.buildEnd}}</span></p>
            </template>
        </div>
        <v-dia v-if="isload"></v-dia>

    </div>
</template>
<script>

import dialog from '@/components/dialog'
export default {
    name: 'yjCon', // 
    data() {
        return {
            // 
            isload:false
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        obj:{},
        type:{
            default:'yj1'
        }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        // this.conver()
        if( sessionStorage.getItem('permissions') == null || sessionStorage.getItem('permissions') == '' || sessionStorage.getItem('permissions').indexOf('bidFilter') == -1  ) {
            if(this.obj.oneName){
                this.obj.oneName=this.getPassOnename(this.obj.oneName)
            }
            if( this.obj.oneOffer) {
                this.obj.oneOffer=this.getPassOneoffer(this.obj.oneOffer)
            } 
        }
    },
    components: {
        'v-dia':dialog
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        conver() {
             this.obj.opendate =  this.obj.opendate.replace('-','')
             this.obj.opendate =  this.obj.opendate.replace('-','')
             this.obj.opendate = this.obj.opendate +  ''             
        },
        topush(o) {
            if(sessionStorage.getItem('xtoken')) {
                if(this.type=='project'){
                    this.$router.push({path:'/zjDetail',query:{id:o.proId}})
                }
                
            } else {
                this.isload = true 
            }
            
        },
        noFollow(){
            this.$emit('nofollow',{id:this.obj.id,source:this.obj.source})
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.tit{
    font-size: 32px;
    font-weight: bold;
    color: #000;
    margin-bottom: 26px;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space:nowrap
}
.label{
    padding: 11px 15px;
    font-size: 20px;
    margin-bottom: 24px;
    display: inline-block;
}
.zj{
    color: #EF873A;
    background: #FDEFE3;
}
.sl{
    background: #FFE5F0;
    color: #D65588;
}
.jt{
    background: #E3E6FD;
    color: #3A76F0;
}
p{
    font-size: 28px;
    color: #999;
    margin-bottom: 26px;
    overflow: hidden;
    span{
        color: #333;
    }
}
p:last-child{
    margin-bottom: 0
}
.newNotice .top{
    margin-bottom: 0;
}
</style>



