<!--  DOM  -->
<template>
    <div class="listCon">
        <!--  -->
        <template v-if="type==0">
            <h5>
                <span>{{obj.zhongbiaoCode}}</span>
                <van-icon name="arrow" class="nav-i"/>
            </h5>
            <p>
               <span class="tit-txt"></span>
               <span>{{obj.zhongbiaoCompany}}</span>
            </p>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.zhongbiaoAmount}}</span>
            </p>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.zhongbiaoDate}}</span>
            </p>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.zhaobiaoType}}</span>
            </p>
        </template>
        <!--  -->
        <template v-else-if="type==1">
            <h5>
                <span>{{obj.checkNo}}</span>
                <van-icon name="arrow" class="nav-i"/>
            </h5>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.checkOrg}}</span>
            </p>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.checkFinishDate}}</span>
            </p>
        </template>
        <!--  -->
        <template v-else-if="type==2">
            <h5>
                <span>{{obj.provRecordCode}}</span>
                <van-icon name="arrow" class="nav-i"/>
            </h5>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.contractComName}}</span>
            </p>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.letContractComName}}</span>
            </p>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.type}}</span>
            </p>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.amount}}</span>
            </p>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.signDate}}</span>
            </p>
        </template>
        <!--  -->
        <template v-else-if="type==3">
            <h5>
                <span>{{obj.blicence}}</span>
                <van-icon name="arrow" class="nav-i"/>
            </h5>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.area}}</span>
            </p>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.amount}}</span>
            </p>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.licenceDate}}</span>
            </p>
        </template>
        <!--  -->
        <template v-else-if="type==4">
            <h5>
                <span>{{obj.provCode}}</span>
                <van-icon name="arrow" class="nav-i"/>
            </h5>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.cost}}</span>
            </p>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.area}}</span>
            </p>
            <p>
                <span class="tit-txt"></span>
                <span>{{obj.buildEnd}}</span>
            </p>
        </template>
    </div>
</template>
<script>
// let moment = require("moment");
export default {
    name: 'listCon', // 
    data() {
        return {
            // 
        }
    },
    watch: {
        // 
    },
    props: {
        // 
        obj:{},
        type:{
            default:0
        }
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {

        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        // 
    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang="less">
.listCon{
    background: #fff;
    margin-bottom: 16px;
    border-radius: 8px;
    padding-bottom: 28px;
    h5{
        padding:0 20px;
        height: 80px;
        border-bottom: 1PX solid #F0F0F0;
        font-size: 28px;
        // font-weight: normal;
        color: #333;
        margin-bottom: 26px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        span{
            overflow: hidden;
            text-overflow:ellipsis;
            white-space:nowrap;
            display: inline-block;
        }
    }
    p{
        padding:0 20px;
        margin-bottom: 26px;
        overflow: hidden;
        .tit-txt{
            min-width: 112px;
            display: inline-block;
            text-align-last: justify;
        }
        span{
            font-size: 26px;
            color: #999;
        }
    }
}
</style>



