<!--  DOM  -->
<template>
    <div class="zjDetail">
      <top-back :title="''"></top-back>
      <div class="letter-de">
        <!--  -->
        <div class="content">
          <h5>{{detail.proName}}</h5>
          <p>
              <span class="tit-txt"></span>
              <span class="val-txt">{{detail.proType}}</span>  
          </p>
          <p>
              <span class="tit-txt"></span>
              <span class="val-txt">{{detail.proOrg}}</span>  
          </p>
          <p>
              <span class="tit-txt"></span>
              <span class="val-txt">{{detail.investAmount}}</span>  
          </p>
          <p>
              <span class="tit-txt"></span>
              <span class="val-txt">{{detail.proLevel}}</span>  
          </p>
          <p>
              <span class="tit-txt"></span>
              <span class="val-txt">{{detail.buildType}}</span>  
          </p>
          <p>
              <span class="tit-txt"></span>
              <span class="val-txt">{{detail.acreage}}</span>  
          </p>
          <p>
              <span class="tit-txt"></span>
              <span class="val-txt">{{detail.proWhere}}</span>  
          </p>
          <p>
              <span class="tit-txt"></span>
              <span class="val-txt">{{detail.proUse}}</span>  
          </p>
          
        </div>
        <!-- navnav -->
        <div class="letter-nav">
          <div>
              <span v-for="(o,i) of navList" :key="i" :class="navNum==i?'active':''" @click="jump(i)">{{o}}</span>
          </div>
        </div>
        <div class="letter-detail">
            <template>
              <!--  -->
              <v-list :type="navNum"></v-list>
            </template>

            <!-- <template v-else-if="navNum==1"> -->
              <!--  -->
              <!-- <v-list :type="navNum"></v-list> -->
            <!-- </template> -->
            <!-- <template v-else-if="navNum==2"> -->
              <!--  -->
              <!-- <v-list :type="navNum"></v-list> -->
            <!-- </template> -->
            <!-- <template v-else-if="navNum==3"> -->
              <!--  -->
              <!-- <v-list :type="navNum"></v-list> -->
            <!-- </template> -->
            <!-- <template v-else-if="navNum==4"> -->
              <!--  -->
              <!-- <v-list :type="navNum"></v-list> -->
            <!-- </template> -->
        </div>

      </div>
    </div>
</template>
<script>
import topBack from '@/components/topback'
import list from '@/components/yj/list'
export default {
    name: 'zjDetail', // 
    data() {
        return {
            // 
             detail:{},
             navList:['','','','',''],
             navNum:0,
             isload:true,
             vipStr:'',
             isvip:false,
             id:''
            //  path:'/commerc'
        }
    },
    watch: {
        // 
    },
    components: {
      'top-back':topBack,
      'v-list':list,
    },
    props: {
        // 
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        if(sessionStorage.getItem('permissions')){
          this.vipStr=sessionStorage.getItem('permissions');
        }
        this.id = this.$route.query.id
        let that=this;
        this.$http({
            method:'post',
            url: '/project/detail',
            data:{
                id:this.id
            }
        }).then(function(res){
            that.detail = res.data.data;
          })
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        jump(i){
          this.navNum=i
        }

    }

}

</script>
<!--  "scoped"   CSS  -->
<style scoped lang='less'>
.zjDetail {    
  padding-top: 100px;
  min-height:100vh;
  background: #f5f5f5;
  box-sizing: border-box;
  .le-col{
    color:#FE6603;
  }
  .letter-de {
    background-color: #f5f5f5;
  }
}
.content{
  padding: 36px 32px;
  margin-bottom: 16px;
  background: #fff;
  h5{
    color: #333333;
    font-size: 32px;
    margin-bottom: 36px
  }
  p{
    font-size: 28px;
    margin-bottom: 23px;
    .tit-txt{
      color: #999;
      margin-right: 32px;
      width: 112px;
      display: inline-block;
      text-align-last: justify;
    }
    .val-txt{  
      color: #333;
    }
  }
}


.letter-nav {
  height: 96px;
  background-color: #fff;
//  padding: 0 32px;
  overflow-x:scroll;
  line-height: 96px;
  border-bottom: 1PX solid #F2F2F2;
  div{
    width: 126%;
    span{
      min-width: 20%;
      display: inline-block;
      text-align: center;
    }
    .active{
      color: #FE6603
    }
  }
   
}

  
</style>



